.poem {
    padding: 10px;
    box-sizing: border-box;
}
.poem.hover {
    cursor: pointer;
}
.poem.hover:hover {
    background-color: #403d3d;
}
.title {
    font-size: 1.5em;
    font-weight: 700;
    text-transform: uppercase;
}

.author {
    font-size: 1.2em;
    font-weight: 500;
    font-family: cursive;
    text-transform: capitalize;
}

.poemContent {
    box-sizing: border-box;
    z-index: 3;
    color: white;
    max-height: 33.333vh;
    height: 100%;
    padding: 30px;
    position: relative;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    overflow: hidden;
}
.poemContent:not(:last-child) {
    border-bottom: 5px white solid;
}

.poemContent.hover {
    cursor: pointer;
}
.poemContainer {
    box-sizing: border-box;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    overflow: auto;
    background-color: #2d2d2d;
}

.poemText {
    line-height: 2em;
    font-size: 1.2em;
    padding: 50px;
    word-break: break-word;
    color: white;
    z-index: 3;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.poemText > * {
    width: calc(50% - 15px);
}

.buttons {
    margin-left: 50px;
}
.buttons.rtl {
    margin-left: 0px;
    margin-right: 50px;
}

.leftBorder {
    position: absolute;
    bottom: 0;
    left: -10px;
    border-left: 2px solid #ffffffc4;
    height: 100%;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-animation: in 0.41s 0.2s 1 ease-in-out forwards;
            animation: in 0.41s 0.2s 1 ease-in-out forwards;
    z-index: 3;
}
.leftBorder.rtl {
    left: unset;
    right: -10px;
}

.leftBorder.out {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-animation: out 0.3s 1 ease-in-out forwards;
            animation: out 0.3s 1 ease-in-out forwards;
}
@-webkit-keyframes in {
    100% {
        -webkit-transform: scaleY(1);
                transform: scaleY(1);
    }
}
@keyframes in {
    100% {
        -webkit-transform: scaleY(1);
                transform: scaleY(1);
    }
}
@-webkit-keyframes out {
    100% {
        -webkit-transform: scaleY(0);
                transform: scaleY(0);
    }
}
@keyframes out {
    100% {
        -webkit-transform: scaleY(0);
                transform: scaleY(0);
    }
}

.exit > path {
    stroke: white;
}
.exit:hover > path {
    stroke: #2d2d2d;
}
.exit {
    cursor: pointer;
    height: 24px;
    width: 24px;
    position: absolute;
    right: 20px;
    opacity: 0;
    -webkit-transition: opacity 0.2s ease-in-out;
    transition: opacity 0.2s ease-in-out;
    z-index: -2;
}
.exit.show {
    z-index: 10;
    opacity: 1;
}

.exit.out {
    z-index: -10;
    opacity: 0;
}

